	module.exports = {"data":{"markdownRemark":{"html":"<p>With MantisBT integration, HackerOne makes it easy for you to track MantisBT issues as references on the platform.</p>\n<p>In order to configure MantisBT integration for your team, contact HackerOne with the following information:   </p>\n<li> The base URI of the MantisBT instance (e.g., \"https://mantis.company.com/\")\n<li> The project (project_id) you'd like issues to default to (an integer)\n<li> The category (category_id) you'd like issues to default to (an integer)\n<li> Any other custom/specific fields you'd like prepopulated. Options are described here: https://www.mantisbt.org/wiki/doku.php/mantisbt:customizing_columns_in_view_issues_page#column_names\n<p>With all of the provided information, HackerOne will be able to set up your requested integration. You’ll get an email notification letting you know that your integration has been set up within 1-2 business days.</p>\n<h3>How the Integration Works</h3>\n<p>After your MantisBT integration has been set up:</p>\n<ol>\n<li>Change the action picker to <strong>Change state > Triaged</strong> in your report. </li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-c485f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 48.708010335917315%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"integrations\"\n        title=\"\"\n        src=\"/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-e7ffa.png\"\n        srcset=\"/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-49813.png 125w,\n/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-dc8a5.png 250w,\n/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-e7ffa.png 500w,\n/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-dda24.png 750w,\n/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-c5f18.png 1000w,\n/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-22484.png 1500w,\n/docs.hackerone.com/static/integrations-f07e0d0182f92c853be1bd8a39743b15-c485f.png 1548w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"2\">\n<li>Click <strong>Escalate</strong>.</li>\n<li>You’ll be taken to your MantisBT account where the report is pre-populated. Submit the issue to create the report in MantisBT.</li>\n<li>Copy the MantisBT report issue number and paste it in the <strong>Reference ID</strong> field of the HackerOne report to create a direct reference link to the issue in MantisBT.  </li>\n</ol>","frontmatter":{"path":"/programs/mantisbt-integration","title":"MantisBT Integration"}}},"pathContext":{}}

